
import GlobalMixin from "@/GlobalMixin.vue";
import { DAMAGE_RESULT_TEMPLATE } from "@/input";
import { ELEMENT_COLOR_CLASS, TElementColorClassKey } from "@/master";
import { defineComponent, reactive, ref } from "vue";

export default defineComponent({
  name: "DamageResult",
  mixins: [GlobalMixin],
  props: {
    damageResult: {
      type: Object,
      required: true,
    },
  },
  setup(props) {
    const damageResultRea = reactive(props.damageResult ?? DAMAGE_RESULT_TEMPLATE);
    const  = reactive(props.damageResult?. ?? damageResultRea.);
    const  = ref("");
    const elementClass = (item: string) =>
      ELEMENT_COLOR_CLASS[item as TElementColorClassKey];

    const displayDamageValue = (item: any, index: number) => {
      let value = item[index];
      if (!value) return "-";
      if (![""].includes(item[5])) {
        if (.value == "" && ["", "", ""].includes(item[1])) {
          value *= .;
        } else if (.value == "" && ["", "", ""].includes(item[1])) {
          value *= .;
        }
      }
      if (value < 10) value = Math.round(value * 100) / 100;
      else if (value < 100) value = Math.round(value * 10) / 10;
      else value = Math.round(value);
      return value;
    };

    const CATEGORY_LIST = [
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const itemList = (category: string) => {
      return damageResultRea[category].filter((s: any[]) => !s[0].startsWith(""));
    };

    return {
      damageResultRea,
      ,
      ,
      elementClass,
      displayDamageValue,
      CATEGORY_LIST,
      itemList,
    };
  },
});
